.iconos-nosotros{
    @include tablet {
        @include grid(3, 2rem);
    }
}

.icono {
    text-align: center;

    img {
        height: 10rem;
        margin: auto;
    }
    h3 {
        text-transform: uppercase;
        color: $primario;
    }
    p {
        color: $blanco;
    }
}