
.borrar-curso {
    background-color: red;
    border-radius: 50%;
    padding: 5px 10px;
    text-decoration: none;
    color: white;
    font-weight: bold;
}
.submenu {
    position: relative;
}

.submenu #carrito{
    display: none;
}
.submenu:hover #carrito{
    display: block;
    position: absolute;
    right:0;

    top:100%;
    background-color: white;
    padding: 20px;
    min-height: 400px;
    min-width: 300px;
    z-index: 2;
}
.vacio {
    padding: 10px;
    background-color: crimson;
    text-align: center;
    border-radius: 10px;
    color: white;
}
#img-carrito {
    margin-top: 1rem;

    @include tablet {
        width: 2.1rem;
    }
}